'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'
import { apiHelpers } from '@/lib/apiClient'

interface UserProfile {
  id: string
  name: string
  email: string
  bio: string
  currentLevel: number
  xpPoints: number
  nextLevelXP: number
  joinDate: string
  profileImage?: string
}

interface Skill {
  id: string
  name: string
  level: number
  category: string
}

interface Certification {
  id: string
  name: string
  issuer: string
  dateEarned: string
  credentialId?: string
}

interface Education {
  id: string
  institution: string
  degree: string
  field: string
  startDate: string
  endDate: string
  gpa?: string
}

interface Connection {
  id: string
  name: string
  title: string
  company: string
}

export default function ProfilePage() {
  const [profile, setProfile] = useState<UserProfile | null>(null)
  const [skills, setSkills] = useState<Skill[]>([])
  const [certifications, setCertifications] = useState<Certification[]>([])
  const [education, setEducation] = useState<Education[]>([])
  const [connections, setConnections] = useState<Connection[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  // Mock data for demonstration
  const mockProfile: UserProfile = {
    id: '1',
    name: 'Alex Thompson',
    email: 'alex.thompson@email.com',
    bio: 'Passionate about technology and data science. Currently exploring AI/ML applications in business. Love connecting with like-minded professionals and sharing knowledge.',
    currentLevel: 7,
    xpPoints: 2450,
    nextLevelXP: 3000,
    joinDate: '2024-01-15',
    profileImage: undefined
  }

  const mockSkills: Skill[] = [
    { id: '1', name: 'Python', level: 85, category: 'Programming' },
    { id: '2', name: 'Data Analysis', level: 90, category: 'Analytics' },
    { id: '3', name: 'Machine Learning', level: 75, category: 'AI/ML' },
    { id: '4', name: 'SQL', level: 80, category: 'Database' },
    { id: '5', name: 'Communication', level: 88, category: 'Soft Skills' },
    { id: '6', name: 'Project Management', level: 70, category: 'Management' }
  ]

  const mockCertifications: Certification[] = [
    {
      id: '1',
      name: 'Google Data Analytics Certificate',
      issuer: 'Google',
      dateEarned: '2024-03-15',
      credentialId: 'GDA-2024-001'
    },
    {
      id: '2',
      name: 'AWS Cloud Practitioner',
      issuer: 'Amazon Web Services',
      dateEarned: '2024-02-10',
      credentialId: 'AWS-CP-2024'
    },
    {
      id: '3',
      name: 'Scrum Master Certification',
      issuer: 'Scrum Alliance',
      dateEarned: '2023-11-20',
      credentialId: 'CSM-2023-456'
    }
  ]

  const mockEducation: Education[] = [
    {
      id: '1',
      institution: 'University of Technology',
      degree: 'Bachelor of Science',
      field: 'Computer Science',
      startDate: '2020-09-01',
      endDate: '2024-05-15',
      gpa: '3.7'
    },
    {
      id: '2',
      institution: 'DataCamp',
      degree: 'Professional Certificate',
      field: 'Data Science',
      startDate: '2023-06-01',
      endDate: '2024-01-15'
    }
  ]

  const mockConnections: Connection[] = [
    { id: '1', name: 'Sarah Johnson', title: 'Senior Data Scientist', company: 'TechCorp' },
    { id: '2', name: 'Mike Chen', title: 'Product Manager', company: 'InnovateLab' },
    { id: '3', name: 'Emily Rodriguez', title: 'UX Designer', company: 'DesignStudio' },
    { id: '4', name: 'David Kim', title: 'Software Engineer', company: 'CodeWorks' }
  ]

  useEffect(() => {
    const fetchProfileData = async () => {
      try {
        setLoading(true)
        setError(null)
        
        // Try to fetch from API, fallback to mock data
        try {
          const [profileData, skillsData, certificationsData, educationData, connectionsData] = await Promise.all([
            apiHelpers.get<UserProfile>('/profile'),
            apiHelpers.get<Skill[]>('/profile/skills'),
            apiHelpers.get<Certification[]>('/profile/certifications'),
            apiHelpers.get<Education[]>('/profile/education'),
            apiHelpers.get<Connection[]>('/profile/connections')
          ])
          
          setProfile(profileData)
          setSkills(skillsData)
          setCertifications(certificationsData)
          setEducation(educationData)
          setConnections(connectionsData)
        } catch (apiError) {
          // Use mock data if API is not available
          console.log('API not available, using mock data')
          setProfile(mockProfile)
          setSkills(mockSkills)
          setCertifications(mockCertifications)
          setEducation(mockEducation)
          setConnections(mockConnections)
        }
      } catch (err) {
        setError('Failed to load profile data')
        // Fallback to mock data
        setProfile(mockProfile)
        setSkills(mockSkills)
        setCertifications(mockCertifications)
        setEducation(mockEducation)
        setConnections(mockConnections)
      } finally {
        setLoading(false)
      }
    }

    fetchProfileData()
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
      </div>
    )
  }

  if (!profile) {
    return (
      <div className="text-center py-12">
        <p className="text-muted-foreground">Profile not found</p>
      </div>
    )
  }

  const progressPercentage = (profile.xpPoints / profile.nextLevelXP) * 100

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Your Profile</h1>
        <p className="text-muted-foreground">
          Manage your professional information and track your progress
        </p>
      </div>

      {error && (
        <Card className="mb-6 border-destructive">
          <CardContent className="pt-6">
            <p className="text-destructive">{error}</p>
          </CardContent>
        </Card>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left Column - Profile Info */}
        <div className="lg:col-span-1 space-y-6">
          {/* Basic Profile */}
          <Card>
            <CardHeader>
              <CardTitle>Profile Information</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="text-center">
                <div className="w-24 h-24 bg-primary rounded-full flex items-center justify-center mx-auto mb-4">
                  <span className="text-2xl font-bold text-primary-foreground">
                    {profile.name.split(' ').map(n => n[0]).join('')}
                  </span>
                </div>
                <h2 className="text-xl font-semibold text-foreground">{profile.name}</h2>
                <p className="text-muted-foreground">{profile.email}</p>
              </div>
              
              <Separator />
              
              <div>
                <h3 className="font-medium text-foreground mb-2">Bio</h3>
                <p className="text-sm text-muted-foreground">{profile.bio}</p>
              </div>
              
              <Separator />
              
              <div>
                <h3 className="font-medium text-foreground mb-2">Level Progress</h3>
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span>Level {profile.currentLevel}</span>
                    <span>{profile.xpPoints} / {profile.nextLevelXP} XP</span>
                  </div>
                  <Progress value={progressPercentage} />
                </div>
              </div>
              
              <Button className="w-full">Edit Profile</Button>
            </CardContent>
          </Card>

          {/* Connections Summary */}
          <Card>
            <CardHeader>
              <CardTitle>Connections</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center mb-4">
                <div className="text-2xl font-bold text-foreground">{connections.length}</div>
                <div className="text-sm text-muted-foreground">Professional Connections</div>
              </div>
              <div className="space-y-2">
                {connections.slice(0, 3).map((connection) => (
                  <div key={connection.id} className="flex items-center space-x-3">
                    <div className="w-8 h-8 bg-muted rounded-full flex items-center justify-center">
                      <span className="text-xs font-medium">
                        {connection.name.split(' ').map(n => n[0]).join('')}
                      </span>
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium text-foreground truncate">
                        {connection.name}
                      </p>
                      <p className="text-xs text-muted-foreground truncate">
                        {connection.title}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
              <Button variant="outline" className="w-full mt-4">
                View All Connections
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Right Column - Detailed Info */}
        <div className="lg:col-span-2 space-y-6">
          {/* Skills */}
          <Card>
            <CardHeader>
              <CardTitle>Skills</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {skills.map((skill) => (
                  <div key={skill.id} className="space-y-2">
                    <div className="flex justify-between items-center">
                      <span className="text-sm font-medium text-foreground">{skill.name}</span>
                      <Badge variant="secondary">{skill.category}</Badge>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Progress value={skill.level} className="flex-1" />
                      <span className="text-sm text-muted-foreground w-12">{skill.level}%</span>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Certifications */}
          <Card>
            <CardHeader>
              <CardTitle>Certifications</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {certifications.map((cert) => (
                  <div key={cert.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-start mb-2">
                      <h3 className="font-medium text-foreground">{cert.name}</h3>
                      <Badge variant="outline">
                        {new Date(cert.dateEarned).getFullYear()}
                      </Badge>
                    </div>
                    <p className="text-sm text-muted-foreground mb-1">
                      Issued by {cert.issuer}
                    </p>
                    <p className="text-xs text-muted-foreground">
                      Earned: {new Date(cert.dateEarned).toLocaleDateString()}
                    </p>
                    {cert.credentialId && (
                      <p className="text-xs text-muted-foreground">
                        Credential ID: {cert.credentialId}
                      </p>
                    )}
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Education */}
          <Card>
            <CardHeader>
              <CardTitle>Education</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {education.map((edu) => (
                  <div key={edu.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-start mb-2">
                      <div>
                        <h3 className="font-medium text-foreground">{edu.degree}</h3>
                        <p className="text-sm text-muted-foreground">{edu.field}</p>
                      </div>
                      {edu.gpa && (
                        <Badge variant="secondary">GPA: {edu.gpa}</Badge>
                      )}
                    </div>
                    <p className="text-sm text-foreground mb-1">{edu.institution}</p>
                    <p className="text-xs text-muted-foreground">
                      {new Date(edu.startDate).getFullYear()} - {new Date(edu.endDate).getFullYear()}
                    </p>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
